import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import store from "../store";
const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("@/views/auth/Login.vue"),
  },
  {
    path: "/register",
    name: "Register",
    component: () => import("@/views/auth/Register.vue"),
  },

  {
    path: "/biology",
    name: "Biology",
    component: () => import("@/views/Biology.vue"),
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/fullsyllabus",
    name: "FullSyllabus",
    component: () => import("@/views/FullSyllabus.vue"),
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/exam/:id",
    name: "ExamQuestion",
    meta: { requiresAuth: true },
    component: () => import("../views/exams/MCQuestion.vue"),
  },
  {
    path: "/ranking/:id",
    name: "Ranking",
    meta: { requiresAuth: true },
    component: () => import("../views/exams/MCQLeaderboard.vue"),
  },
  {
    path: "/:catchAll(.*)*",
    redirect: { name: "home" },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  if (!to.matched.some((record) => record.meta.requiresAuth)) {
    next();
    return;
  }

  if (store.state.userLoggedIn) {
    next();
    return;
  }

  next({ name: "Login" });
});

export default router;
