import { createStore } from "vuex";
import { auth } from "@/plugins/auth";

export default createStore({
  state: {
    userLoggedIn: false,
    user: {},
    theme: "light",
  },
  mutations: {
    toggleAuth(state) {
      state.userLoggedIn = !state.userLoggedIn;
    },
    SET_THEME(state, theme) {
      state.theme = theme;
      localStorage.theme = theme;
    },
    SET_USER(state, user) {
      state.user = user;
    },
  },
  actions: {
    async register({ dispatch }, payload) {
      console.log(payload);
      const result = await fetch(auth, {
        method: "POST",
        body: payload.body,
      });
      const response = await result.json();

      if (response.status == 200) {
        delete payload.body;
        dispatch("login", payload);
      }
    },

    async login({ commit }, payload) {
      const result = await fetch(auth + "?phone=" + payload.phone);
      const response = await result.json();

      if (response.user) {
        let [phone, roll, name, email] = response.user;
        let user = {
          phone,
          roll,
          name,
          email,
        };
        localStorage.setItem("ssrb", JSON.stringify(user));
        commit("SET_USER", user);
        commit("toggleAuth");
        payload.router.push("/");
      } else {
        payload.router.push("/register?phone=" + payload.phone);
      }
    },
    async signOut({ commit }, payload) {
      await auth.signOut();
      commit("toggleAuth");
      if (payload.route.meta.requiresAuth) {
        payload.router.push({ name: "home" });
      }
    },
    initTheme({ commit }) {
      const cachedTheme = localStorage.theme ? localStorage.theme : false;
      //  `true` if the user has set theme to `dark` on browser/OS
      const userPrefersDark = window.matchMedia("(prefers-color-scheme: dark)")
        .matches;

      if (cachedTheme) commit("SET_THEME", cachedTheme);
      else if (userPrefersDark) commit("SET_THEME", "dark");
      else commit("SET_THEME", "light");
    },
  },
  getters: {
    getTheme: (state) => {
      return state.theme;
    },
  },
  modules: {},
});
