<template>
  <nav class="border-b-2 border-gray-300 lato dark:bg-gray-800">
    <div class="flex justify-between h-20 items-center container md:mx-auto">
      <div class="mx-2">
        <router-link to="/" class="relative flex">
          <img
            src="https://img.icons8.com/external-flatart-icons-flat-flatarticons/64/000000/external-free-online-learning-flatart-icons-flat-flatarticons.png"
            class="h-16"
          />
          <h2 class="poppins text-gray-900 text-xl font-bold dark:text-white">
            HSC Short Syllabus <br />
            Revision Plan
          </h2>
        </router-link>
      </div>
      <div class="hidden lg:flex justify-end">
        <div class="hidden md:flex justify-center items-center flex-1 ml-10">
          <router-link
            v-for="(m, i) in menuItems"
            :to="m.link"
            :key="i"
            exact-active-class="bg-gray-700 dark:bg--indigo-400  text-white hover:bg-gray-600  dark:bg-gray-900 shadow-xl"
            class="
              bg-gray-50
              dark:bg-gray-600
              mr-2
              shadow-sm
              hover:bg-gray-200
              rounded-md
              py-2
              px-3
              font-semibold
              dark:text-white
              dark:hover:bg-gray-900
              hover:shadow-lg
              flex
              items-center
            "
          >
            <!-- <i class="fas mr-1" :class="m.icon"></i> -->
            <img :src="m.icon" class="h-8 w-8 mr-2" />
            {{ m.text }}</router-link
          >
          <div class="flex justify-end gap-3">
            <button
              v-if="isLoggedIn"
              class="
                bg-gray-300
                rounded-full
                px-3
                font-semibold
                border border-red-400
                dark:bg-black
                dark:text-white
                shadow
              "
            >
              Roll: {{ user.roll }}
            </button>
            <button @click="toggleTheme" class="shadow-lg rounded-full">
              <img
                src="https://img.icons8.com/bubbles/50/000000/partly-cloudy-night.png"
                v-if="theme == 'light'"
              />
              <img
                src="https://img.icons8.com/color/48/000000/sun--v2.png"
                v-else
              />
            </button>
          </div>
        </div>
      </div>
      <div class="lg:hidden">
        <button
          class="
            bg-gray-100
            px-2
            py-1
            mx-2
            rounded
            hover:bg-gray-300
            outline-none
          "
          @click="mobileMenu = !mobileMenu"
        >
          <i
            class="fas"
            :class="{ 'fa-bars': !mobileMenu, 'fa-times': mobileMenu }"
          ></i>
        </button>
      </div>
    </div>
    <div
      class="
        container
        mx-auto
        p-2
        mb-2
        rounded
        bg-gray-50
        transition-all
        duration-300
        ease-in-out
        transform
      "
      :class="{
        'scale-0': !mobileMenu,
        'scale-100': mobileMenu,
      }"
      v-if="mobileMenu"
    >
      <router-link
        v-for="(m, i) in menuItems"
        :to="m.link"
        :key="i"
        @click="mobileMenu = !mobileMenu"
        class="
          hover:bg-gray-200
          rounded-md
          py-1
          my-1
          px-5
          font-semibold
          flex
          items-center
        "
        exact-active-class="bg-gray-800 text-white hover:bg-gray-600  dark:bg-gray-900 shadow-xl"
      >
        <img :src="m.icon" class="h-8 w-8 mr-2" />{{ m.text }}</router-link
      >
    </div>
    <div class="flex justify-center my-3 gap-3" v-if="mobileMenu">
      <button
        v-if="isLoggedIn"
        class="
          bg-gray-300
          rounded-full
          px-3
          font-semibold
          dark:bg-black
          dark:text-white
          shadow
        "
      >
        Roll: {{ user.roll }}
      </button>
      <button @click="toggleTheme">
        <img
          src="https://img.icons8.com/bubbles/50/000000/partly-cloudy-night.png"
          v-if="theme == 'light'"
        />
        <img src="https://img.icons8.com/color/48/000000/sun--v2.png" v-else />
      </button>
    </div>
  </nav>
</template>

<script>
import { mapGetters, mapState } from "vuex";
export default {
  data() {
    return {
      mobileMenu: false,
      menuItems: [
        {
          text: "Home",
          icon: "https://img.icons8.com/fluency/48/000000/home.png",
          link: "/",
        },
        {
          text: "70 days short syllabus ",
          icon: "https://img.icons8.com/external-flatart-icons-flat-flatarticons/64/000000/external-study-high-school-flatart-icons-flat-flatarticons-1.png",
          link: "/fullsyllabus",
        },
        {
          text: "40 days Biology",
          icon: "https://img.icons8.com/external-flatart-icons-lineal-color-flatarticons/64/000000/external-dna-coronavirus-covid19-flatart-icons-lineal-color-flatarticons.png",
          link: "/biology",
        },
      ],
    };
  },
  methods: {
    toggleTheme() {
      this.$store.commit("SET_THEME", this.theme == "light" ? "dark" : "light");
    },
  },
  computed: {
    ...mapGetters({ theme: "getTheme" }),
    ...mapState({
      isLoggedIn: "userLoggedIn",
      user: "user",
    }),
  },
};
</script>

<style lang="scss" scoped>
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400&display=swap");
.lato {
  font-family: "Lato", sans-serif;
}
</style>